@media (max-width: 1584px) {
  .dragndrop {
    &__content {
      padding-left: 90px;
      padding-right: 90px;
    }
  }
}
@media (max-width: 1440px) {
  .header {
    padding-top: 35px;
    &__inner {
      padding-right: 15px;
      padding-left: 15px;
    }
  }
  .first-screen {
    padding-top: 25vh;
    &__subtitle {
      margin-bottom: 100px;
    }
  }
  .features {
    &__row {
      max-width: 1350px;
      padding-left: 15px;
      padding-right: 15px;
    }
  }
  .features2 {
    &__inner {
      padding-left: 30px;
      padding-right: 30px;
    }
  }
  .description {
    &__content {
      max-width: 1200px;
    }
  }
  .description-info {
    &__line {
      width: 530px;
    }
  }
  .footer {
    &__inner {
      padding-left: 15px;
      padding-right: 15px;
    }
  }
}
@media (max-width: 1280px) {
  body {
    font-size: 16px;
  }
  .title {
    font-size: 4.5rem;
  }
  .first-screen {
    &__subtitle {
      font-size: 3rem;
    }
    &__img {
      max-width: 700px;
      & > img {
        max-width: 80%;
      }
    }
  }
  .features {
    &__title {
      min-height: 60px;
    }
  }
  .features2 {
    &__list-item {
      font-size: 2rem;
    }
  }
  .dragndrop-demo {
    &__title {
      font-size: 2.3rem;
    }
  }
  .description {
    &__subtitle {
      max-width: none;
      padding-right: 100px;
      padding-left: 100px;
    }
    &__content {
      max-width: 900px;
    }
  }
  .description-info {
    &__line {
      width: 390px;
    }
    &--left-top {
      top: 40px;
    }
    &--right-bottom {
      bottom: 100px;
    }
  }
  .pricing {
    &__plan {
      flex-basis: calc(33.333% - 20px);
      // &:not(:last-child) {
      //   margin-right: 30px;
      // }
    }
  }
  .pricing-plan {
    &__price {
      font-size: 3rem;
    }
    &__price-num {
      font-size: 5.5rem;
    }
  }
}
@media (max-width: 991px) {
  .title {
    font-size: 4rem;
  }
  .header {
    padding-top: 20px;
  }
  .first-screen {
    padding-top: 30vh;
  }
  .features {
    padding-top: 200px;
    padding-bottom: 40px;
    &__row {
      flex-wrap: wrap;
      justify-content: space-around;
    }
    &__item {
      margin-bottom: 40px;
      flex-basis: 50%;
      flex-shrink: 0;
    }
    &__btn {
      padding: 15px 55px;
    }
  }
  .features2 {
    padding-top: 0px;
    padding-bottom: 100px;
    &__title {
      white-space: nowrap;
    }
  }
  .dragndrop {
    padding-top: 80px;
    padding-bottom: 100px;
    &__inner {
      flex-direction: column;
    }
    &__demo {
      order: 2;
    }
    &__content {
      margin-bottom: 60px;
    }
    &__text {
      margin-bottom: 20px;
    }
  }
  .pricing {
    padding-bottom: 50px;
    &__title {
      margin-bottom: 40px;
    }
    &__row {
      flex-wrap: wrap;
      justify-content: center;
      margin-bottom: 50px;
    }
    &__text {
      padding-left: 30px;
      padding-right: 30px;
    }
  }
  .pricing-plan {
    padding: 25px 20px;
    &__title {
      margin-bottom: 30px;
    }
    &__price {
      font-size: 2rem;
      margin-bottom: 25px;
      &::after {
        font-size: 1.8rem;
      }
    }
    &__price-num {
      font-size: 3.5rem;
    }
    &__feature {
      font-size: 1.4rem;
    }
  }
}
@media (max-width: 930px) {
  .description {
      &__subtitle {
        padding-right: 50px;
        padding-left: 50px;
      }
    &__content {
      padding-top: 35px;
    }
  }
  .description-info {
    &__line {
      display: none;
    }
    &--left-top {
      left: 15px;
      top:0;
    }
    &--right-bottom {
      right: 15px;
      bottom: 0;
    }
  }
}
@media (max-width: 850px) {
  .burger-btn {
    display: block;
  }
  .header {
    &__content {
      transform: translateY(-100%);
      transition: .3s;
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color: rgba(14, 26, 53, 0.8);
      flex-direction: column;
      padding-top: 50px;
      padding-bottom: 50px;
    }
    &__menu {
      flex-direction: column;
      justify-content: space-between;
      flex-grow: 1;
      // margin-bottom: 40px;
    }
    &__menu-item {
      font-size: 4rem;
      margin-right: 0;
    }
    &__btn {
      order: -1;
      margin-bottom: 40px;
      // margin-right: 50px;
    }
  }
  .pricing {
    &__plan {
      flex-basis: 45%;
    }
  }
}
@media (max-width: 767px) {
  .first-screen {
    &__img {
      background-color: transparent;
      & > img {
        top: 0;
        max-width: 80%;
      }
    }
  }
  .features {
    padding-top: 70px;
  }
  .features2 {
    background-image: none;
    &__inner {

    }
    &__title {
      white-space: normal;
    }
    &__content {
      max-width: 100%;
      // padding-left: 40px;
      // padding-right: 40px;
    }
  }
  .description {
    &__content {
      padding-top: 0;
      flex-direction: column;
      align-items: center;
    }
    &__img {
      order: 3;
    }
  }
  .description-info {
    margin-bottom: 60px;
    max-width: 350px;
    text-align: center;
    &--left-top {
      position: static;
    }
    &--right-bottom {
      position: static;
    }
  }
  .footer {
    &__inner {
      flex-direction: column;
      align-items: center;
    }
    &__copyright {
      margin-bottom: 20px;
    }
  }
}

@media (max-width: 560px) {
  .pricing {
    &__plan {
      flex-basis: 100%;
    }
  }
}
@media (max-width: 480px) {
  .first-screen {
    &__subtitle {
      font-size: 2rem;
    }
  }
  .title {
    font-size: 3rem;
  }
  .header {
    &__logo {
      flex-basis: 60%;
    }
  }
  .features2 {
    &__list-item {
      font-size: 1.8rem;
    }
  }
  .dragndrop {
    &__content {
      padding-left: 0;
      padding-right: 0;
    }
  }
  .description {
    &__subtitle {
      padding-right: 15px;
      padding-left: 15px;
      // font-size: 1.4rem;
    }
  }
  .footer {
    &__links {
      flex-wrap: wrap;
      justify-content: center;
    }
    &__link {
      margin-right: 0;
      flex-basis: 33.333%;
      margin-bottom: 30px;
    }
    &__social {
      flex-basis: 50%;
      justify-content: space-between;
    }
    &__social-link {
      margin-right: 0;
    }
  }
}
